footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);   
}

.footer_logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom:2rem;
    display: inline-block;
}

.footer_links{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 4rem;
}

.footer_socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 3rem;
    
}

.footer_socials a{
    border-radius: 25%;
    background: var(--color-bg);
    color: var(--color-while);
    padding: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer_socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer_copyright{
    margin-bottom: 4rem;
    color:var(--color-bg);
}

/*=========Media Queries (Small divices) =========*/

@media screen  and (max-width: 600px){
    .footer_links{
        flex-direction: column;
        gap:1.5rem;
    }
    .footer_socials{
        margin-bottom: 2.5rem;
    }
}